import { Watchlist } from "@rp/common/api-types"
import { WATCHLIST_ITEMS_MAXIMUM_COUNT } from "@rp/common/settings"
import { UserAvatar } from "@rp/react/web/common/base-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { WatchlistAverageChangePercentageChip } from "@rp/react/web/common/watchlists"
import { Box, Typography } from "@rp/react/web/mui/core"

type Props = {
  watchlist: Watchlist
  ownerName: string | undefined
  imageUrl: string | undefined
  averageChangePercentage: number | null
  displayName: string
}

const WatchlistDetails = ({
  watchlist,
  ownerName,
  imageUrl,
  averageChangePercentage,
  displayName,
}: Props) => {
  const hasReachedLimit = watchlist.items.length >= WATCHLIST_ITEMS_MAXIMUM_COUNT
  return (
    <Box
      sx={{
        display: "flex",
        gap: 1,
        alignItems: "center",
        width: "100%",
        justifyContent: "space-between",
      }}
    >
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          gap: 1,
          maxWidth: "calc(100% - 100px)",
          pr: 1,
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
          <UserAvatar ownerName={ownerName} imgUrl={imageUrl} hasImage={!!imageUrl} size={24} />
          <EllipsisTooltip
            tooltipText={displayName ?? ""}
            typographyProps={{ fontSize: 12, maxWidth: 150 }}
          >
            {displayName}
          </EllipsisTooltip>
        </Box>
        Â·
        <Typography fontSize={12}>
          {`${watchlist.items.length} ${watchlist.items.length === 1 ? "entity" : "entities"}`}
        </Typography>
        <Typography fontSize={12} color="text.secondary">
          {hasReachedLimit ? "(maximum limit reached)" : ""}
        </Typography>
      </Box>
      <WatchlistAverageChangePercentageChip averageChangePercentage={averageChangePercentage} />
    </Box>
  )
}

export { WatchlistDetails }
